
import { getListTask,getDetailInfo, getLogin} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
data(){
return{
infoList:[],
detailInfo:{},
showDialog:false,
show:false,
show1:false,
storyshow:true,
choiceShow:false,
bugshow:false,
confirmshow:false,
show2:false,
list:[]
}
},
directives: {
TransferDom
},
components: {
Group,
Cell,
Datetime,
CellBox,
Confirm,
XInput ,
XTextarea


},
methods:{
agree(){
this.confirmshow = true;
},
reject(){
this.showDialog = true;
},
close(){
this.showDialog = false;
},
change(value){
console.log(value);
},
onCancel(){

},
onConfirm(){

}


},
created(){
var code  = this.$route.query.data;




var token  = localStorage.getItem("token");
var time = new Date().getTime();
console.log(token);
getListTask(code,token).then((result)=>{
this.infoList = result;

})
getDetailInfo(time,token,code).then((result)=>{
if(result.length>=1){
this.detailInfo = JSON.parse(result[0].json_data);
}
var t = JSON.parse(result[0].json_data);
})

},
mounted(){
var self = this;
self.mescroll = new MeScroll("mescroll",{
up:{
isBounce:false,
use:false,
auto:false

},
down:{
use:false,
auto:false,
isLock:false
}
})



}

}




